@charset "UTF-8";
/** Reset y Estilos Globales  **/
@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
/* Modern CSS Reset 2025 - Optimizado para Bienes Raíces MVC */
/* 1. Box sizing global: Fundamental para que el padding no rompa tus layouts */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* 2. Reset de márgenes y tipografía */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* 3. Configuración del documento */
html {
  /* Altura completa y suavizado de scroll */
  height: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  /* Estándar de accesibilidad actual (mejor que 1.15) */
  -webkit-font-smoothing: antialiased;
}

/* 4. Imágenes y Media: Responsivas por defecto */
img,
picture,
svg,
video,
canvas {
  max-width: 100%;
  display: block;
  /* Elimina el espacio extra debajo de las imágenes */
}

/* 5. Formularios: Heredar fuentes (esto ahorra mucho código en el Admin) */
input,
button,
textarea,
select {
  font: inherit;
}

/* 6. Eliminar animaciones para personas con sensibilidad (Accesibilidad) */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.propiedades tbody tr td {
  color: #000000;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

